import React, { Fragment, useEffect, useState } from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import { IoDocumentOutline } from "react-icons/io5";
import { MdBrightness1, MdInfo, MdRemoveCircleOutline } from "react-icons/md";
import { ImFilesEmpty } from "react-icons/im";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../actions/document";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  CandidateDetailsInterface,
  CandidatesInterface,
} from "../../shared/interfaces";
import { countByStatus } from "./DocumentApplChecking";
import DocumentLoading from "./DocumentLoading";
import { isEmptyOrSpaces } from "../../shared/dataTest";

export const GetDocTitle = (
  documentCategories: DocumentReducerInterface,
  doc: CandidateDetailsInterface
): { category: string; sub_category: string; field: string; main: string } => {
  let DocCategory: string = "",
    DocSubCategory: string = "",
    DocField: string = "",
    DocTitle: string = "";
  if (
    doc.sub_category_id &&
    doc.sub_category_id.length > 0 &&
    documentCategories.documentSubCategory
  ) {
    DocTitle = documentCategories.documentSubCategory.find(
      (c) => c.user_detail_subcategory_id === doc.sub_category_id
    )
      ? documentCategories.documentSubCategory.find(
          (c) => c.user_detail_subcategory_id === doc.sub_category_id
        )!.title
      : "Unknown qualification";
    DocSubCategory = DocTitle;
  } else if (
    doc.category_id.length > 0 &&
    documentCategories.documentCategory
  ) {
    DocTitle = documentCategories.documentCategory.find(
      (c) => c.user_detail_category_id === doc.category_id
    )
      ? documentCategories.documentCategory.find(
          (c) => c.user_detail_category_id === doc.category_id
        )!.title
      : "Unknown category";
    DocCategory = DocTitle;
  }
  if (
    doc.category_subcategory_data.length > 0 &&
    documentCategories.documentFields
  ) {
    let thisDocField = documentCategories.documentFields.find(
      (c) => c._id === doc.category_subcategory_data
    );
    DocField = thisDocField ? thisDocField.title : "Unknown filed";
    DocTitle += " in " + DocField;
  }

  return {
    category: DocCategory,
    sub_category: DocSubCategory,
    field: DocField,
    main: DocTitle,
  };
};

export const countFileByCandidateId = (
  userDocs: CandidateDetailsInterface[],
  filterType: "docCategory" | "docSubCategory" | "docStatus",
  filterValue: string,
  filterStatus: "waiting" | "approved" | "rejected" = "waiting"
): number => {
  let result: CandidateDetailsInterface[] = userDocs;
  // result = userDocs.filter(
  //   (details) => details.validation_status === docStatus && details.files
  // );
  if (filterType === "docCategory") {
    result = userDocs.filter(
      (details) =>
        details.category_id === filterValue &&
        details.files &&
        details.files.length > 0 &&
        details.validation_status === filterStatus
    );
  } else if (filterType === "docSubCategory") {
    result = userDocs.filter(
      (details) =>
        details.sub_category_id === filterValue &&
        details.files &&
        details.files.length > 0 &&
        details.validation_status === filterStatus
    );
  }
  // if (docStatus === "approved") {
  //   result = userDocs.filter(
  //     (details) => details.validation_status !== "waiting"
  //   );
  // }
  return result.length;
};

interface AppProps {
  candidate: CandidatesInterface;
  documentCategories: DocumentReducerInterface;
  candidateCheckData: CandidatesInterface | null;
  filterByCategoryId: string;
  filterBySubCategoryId: string;
  setCandidateId: (data: string) => void;
  setDocumentId: (data: string) => void;
  setViewDoc: (data: boolean) => void;
  setDocument_index: (data: number | null) => void;
  getDocumentFields: Function;
}
const ApplUserDocuments: React.FC<AppProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [tabsController, setTabsController] = useState<
    "waiting" | "approved" | "rejected"
  >("waiting");
  const [filterByCategory, setFilterByCategory] = useState<string>(
    props.filterByCategoryId
  );
  const [filterBySubCategory, setFilterBySubCategory] = useState<string>(
    props.filterBySubCategoryId
  );
  const [notVerified, setNotVerified] = useState<boolean>(false);

  useEffect(() => {
    if (!props.documentCategories.documentFields && loading) {
      props.getDocumentFields();
    }
    if (props.documentCategories.documentFields && loading) {
      setLoading(false);
    }
  }, [loading, props]);

  const filterDocuments = (
    details: CandidateDetailsInterface[],
    cateogry: string,
    subCategory: string
  ): CandidateDetailsInterface[] => {
    let result: CandidateDetailsInterface[] = [];
    if (cateogry.length > 0 && subCategory.length > 0) {
      result = details.filter(
        (detail) =>
          detail.sub_category_id === subCategory &&
          detail.category_id === cateogry
      );
    } else if (cateogry.length > 0) {
      result = details.filter((detail) => detail.category_id === cateogry);
    } else {
      result = details;
    }

    return result;
  };

  return (
    <div className="w-full flex bg-gray-100 h-full flex-col md:flex-row md:items-start">
      {/* left side for filter */}
      <section className="bg-white h-full w-1/6 p-2 m-3 border border-gray-200 rounded">
        {notVerified && (
          <section
            className={`${
              filterByCategory === "all" || isEmptyOrSpaces(filterByCategory)
                ? "bg-blue-500 text-white font-bold"
                : "bg-blue-100 text-blue-800"
            } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex cursor-pointer items-center animate__animated animate__fadeIn`}
            onClick={() => {
              setFilterByCategory("");
              setFilterBySubCategory("");
            }}
          >
            <section className="flex-1">
              <MdBrightness1 className="inline-block mr-1 text-xs" />
              <span>All</span>
            </section>

            {/* {count > 0 ? ( */}
            <span className="px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full">
              {props.candidate &&
                countByStatus(
                  filterDocuments(
                    props.candidate.details,
                    props.filterByCategoryId,
                    props.filterBySubCategoryId
                  ),
                  "waiting"
                )}
            </span>
            {/* ) : null} */}
          </section>
        )}
        {props.documentCategories.documentCategory &&
          props.documentCategories.documentCategory.map((category, index) => (
            <Fragment key={index + category.user_detail_category_id}>
              {countFileByCandidateId(
                props.candidate.details,
                "docCategory",
                category.user_detail_category_id
              ) > 0 && (
                <section>
                  {!notVerified && setNotVerified(true)}
                  <section
                    className={`${
                      filterByCategory === category.user_detail_category_id
                        ? "bg-blue-500 text-white font-bold"
                        : "bg-blue-100 text-blue-800"
                    } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex cursor-pointer items-center animate__animated animate__fadeIn`}
                    onClick={() => {
                      setFilterByCategory(category.user_detail_category_id);
                      setFilterBySubCategory("");
                    }}
                  >
                    <section className="flex-1">
                      <MdBrightness1 className="inline-block mr-1 text-xs" />
                      <span>{category.title}</span>
                    </section>

                    <span className="px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full">
                      {countFileByCandidateId(
                        props.candidate.details,
                        "docCategory",
                        category.user_detail_category_id
                      )}
                    </span>
                  </section>

                  {/* sub filter */}
                  {filterByCategory === category.user_detail_category_id &&
                    props.documentCategories.documentSubCategory &&
                    props.documentCategories.documentSubCategory
                      .filter(
                        (item) =>
                          item.category_id === category.user_detail_category_id
                      )
                      .map(
                        (subCategory, i) =>
                          countFileByCandidateId(
                            props.candidate.details,
                            "docSubCategory",
                            subCategory.user_detail_subcategory_id
                          ) > 0 && (
                            <div
                              key={i + subCategory.user_detail_subcategory_id}
                              className="ml-3 border-l-2 border-blue-200 -mt-1 pt-2 mb-2 pl-1"
                            >
                              <div
                                className={`animate__animated animate__fadeIn animate__faster ${
                                  filterBySubCategory ===
                                  subCategory.user_detail_subcategory_id
                                    ? "bg-blue-500 text-white font-bold"
                                    : " text-blue-800"
                                } bg-blue-200 rounded-md px-1 mb-1 flex items-center py-1 font-bold`}
                                onClick={() => {
                                  setFilterBySubCategory(
                                    subCategory.user_detail_subcategory_id
                                  );
                                }}
                              >
                                <section className="flex-1">
                                  <MdBrightness1
                                    className={`inline-block mr-0.1 text-xs ${
                                      filterBySubCategory ===
                                      subCategory.user_detail_subcategory_id
                                        ? "text-white"
                                        : ""
                                    }`}
                                  />
                                  <span>{subCategory.title}</span>
                                </section>
                                <span
                                  className={`px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full`}
                                >
                                  {countFileByCandidateId(
                                    props.candidate.details,
                                    "docSubCategory",
                                    subCategory.user_detail_subcategory_id
                                  )}
                                </span>
                              </div>
                            </div>
                          )
                      )}

                  {/* end sub filter */}
                </section>
              )}
            </Fragment>
          ))}
        {!notVerified && (
          <section
            className={`bg-blue-100 text-blue-800 mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex cursor-pointer items-center animate__animated animate__fadeIn`}
          >
            <section className="flex-1">
              <MdBrightness1 className="inline-block mr-1 text-xs" />
              <span>All Are Verified</span>
            </section>
          </section>
        )}
      </section>
      {/* end left side for filter */}

      {/* rigth side */}
      <section className="w-5/6 h-full my-3 mr-3">
        {/* tabs status */}
        <div className="w-full bg-white flex items-center justify-evenly border border-gray-200 rounded">
          <section
            className="cursor-pointer"
            onClick={() => setTabsController("waiting")}
          >
            <div
              className={`flex items-center py-4 px-2 text-yellow-800 gap-1 ${
                tabsController === "waiting" && "border-b-4 border-yellow-800"
              } animate__animated animate__pulse`}
            >
              <MdInfo className="w-6 h-6" />
              <span>Pending</span>
              <span>
                (
                {props.candidate &&
                  countByStatus(
                    filterDocuments(
                      props.candidate.details,
                      props.filterByCategoryId,
                      props.filterBySubCategoryId
                    ),
                    "waiting"
                  )}
                )
              </span>
            </div>
          </section>
          <section
            className="cursor-pointer"
            onClick={() => setTabsController("rejected")}
          >
            <div
              className={`flex items-center py-4 px-2 text-gray-700 gap-1 ${
                tabsController === "rejected" && "border-b-4 border-gray-700"
              } animate__animated animate__pulse`}
            >
              <MdRemoveCircleOutline className="w-6 h-6" />
              <span>Rejected</span>
              <span>
                (
                {props.candidate &&
                  countByStatus(
                    filterDocuments(
                      props.candidate.details,
                      props.filterByCategoryId,
                      props.filterBySubCategoryId
                    ),
                    "rejected"
                  )}
                )
              </span>
            </div>
          </section>
          <section
            className="cursor-pointer"
            onClick={() => setTabsController("approved")}
          >
            <div
              className={`flex items-center py-4 px-2 text-blue-700 gap-1 ${
                tabsController === "approved" && " border-b-4 border-blue-700"
              } animate__animated animate__pulse`}
            >
              <FaRegCheckCircle className="w-6 h-6" />
              <span>Approved</span>
              <span>
                (
                {props.candidate &&
                  countByStatus(
                    filterDocuments(
                      props.candidate.details,
                      props.filterByCategoryId,
                      props.filterBySubCategoryId
                    ),
                    "approved"
                  )}
                )
              </span>
            </div>
          </section>
        </div>
        {/* end tabs status */}

        {/* list of docs */}
        <section className="w-full h-full my-3">
          {/* doc card */}
          {props.candidate &&
          filterDocuments(
            props.candidate.details,
            filterByCategory,
            filterBySubCategory
          ).filter(
            (itm) =>
              itm.validation_status === tabsController && itm.files.length > 0
          ).length > 0 ? (
            <section className="animate__animated animate__fadeIn animate__faster">
              {loading ? (
                <DocumentLoading />
              ) : (
                filterDocuments(
                  props.candidate.details,
                  filterByCategory,
                  filterBySubCategory
                )
                  .filter(
                    (itm) =>
                      itm.validation_status === tabsController &&
                      itm.files.length > 0
                  )
                  .map((item, index) => (
                    <div
                      key={index + item.doc_id}
                      className="bg-white border border-gray-200 hover:border-blue-500 cursor-pointer rounded mb-3 p-4 flex justify-between items-start animate__animated animate__fadeIn group"
                      onClick={() => {
                        props.setViewDoc(true);
                        props.setDocumentId(item.doc_id);
                        props.setDocument_index(index);
                        props.setCandidateId(props.candidate.user_id);
                      }}
                    >
                      {/* doc title */}
                      <div>
                        <h2 className="text-balck font-semibold text-xl">
                          {GetDocTitle(props.documentCategories, item).main}
                        </h2>
                        {/* status */}
                        <div className="flex items-center gap-2">
                          <span className="text-gray-500 italic">Status:</span>
                          <span
                            className={`text-${
                              item.validation_status === "waiting"
                                ? "yellow"
                                : item.validation_status === "approved"
                                ? "blue"
                                : "gray"
                            }-800`}
                          >
                            {item.validation_status === "waiting"
                              ? "Pending"
                              : item.validation_status === "approved"
                              ? "Approved"
                              : "Rejected"}
                          </span>
                        </div>
                        {/* end status */}
                      </div>
                      {/* end doc title */}
                      {/* {console.log({
                    fields: documentCategories.documentFields,
                    fieldId: item.,
                  })} */}
                      <button className="bg-blue-500 p-2 text-white flex rounded items-center space-x-2 focus:outline-none group-hover:bg-blue-600">
                        <IoDocumentOutline className="w-5 h-5" />
                        View doc
                      </button>
                    </div>
                  ))
              )}
            </section>
          ) : (
            <div className="bg-white border border-gray-200 rounded mb-3 p-4 text-gray-400 text-center animate__animated animate__pulse">
              <ImFilesEmpty className="w-28 h-28 mx-auto mb-3" />
              <span className="text-2xl font-mono font-semibold tracking-tighter">
                No Data Found
              </span>
            </div>
          )}

          {/* end doc card */}
        </section>
        {/* end list of docs */}
      </section>
      {/* end rigth side */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
})(ApplUserDocuments);
