import React, { useState } from "react";
import { AiOutlineFullscreen } from "react-icons/ai";
import { BiZoomIn, BiZoomOut } from "react-icons/bi";
import { CgImage } from "react-icons/cg";
import { FaRegCheckCircle } from "react-icons/fa";
import { GrNext, GrPrevious } from "react-icons/gr";
import {
  ImCheckboxChecked,
  ImCheckboxUnchecked,
  ImFolderOpen,
} from "react-icons/im";
import { IoMdClose } from "react-icons/io";
import { MdCancel } from "react-icons/md";
import { RiArrowGoBackFill } from "react-icons/ri";
import { connect } from "react-redux";
import ViewPDF from "../../components/ViewPDF/ViewPDF";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import {
  CandidateDetailsInterface,
  CandidatesInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  fullScreen,
  getFileType,
  isFileImage,
  zoomInOut,
} from "../../utils/functions";
import { GetDocTitle } from "./ApplUserDocuments";
import { DocumentInArray, TableCandidatesInterface } from "./ContentByDocument";
import {
  DocCheckingContentType,
  getCandidateFileByIndex,
  getDocumentFileByIndex,
} from "./DocumentApplChecking";

interface FooterResponsePropInterface {
  actionTaken: "" | "approval" | "rejection";
  responseMsg: string;
  loadingRequest: boolean;
  error: {
    element: string;
    msg: string;
  };
  isTeachingDocument: boolean;
  setIsTeachingDocument: (state: boolean) => void;
  hasDisabilityDocument: boolean;
  setHasDisabilityDocument: (state: boolean) => void;
  setActionTaken: (state: "" | "approval" | "rejection") => void;
  setResponseMsg: (state: string) => void;
  handleVerify: () => Promise<void>;
  setError: (data: { element: string; msg: string }) => void;
}

const FooterResponse: React.FC<FooterResponsePropInterface> = (
  props
): JSX.Element => {
  return (
    <div className="flex flex-col md:flex-col items-end gap-1 w-full overflow-x-auto">
      {/* right part */}
      <div className="flex gap-2 w-full px-2">
        <span className="text-gray-500 italic">Decision:</span>
        <span
          className={`${
            props.actionTaken === "approval" ? "text-blue-700" : "text-red-800"
          } text-xl font-medium`}
        >
          {props.actionTaken === "approval" ? "Approved" : "Rejected"}
        </span>
      </div>
      {props.actionTaken === "approval" && (
        <div className="w-full px-2 mb-1 flex gap-2 items-center">
          <span className="text-gray-500 italic">
            Is Document for teaching:
          </span>
          <div
            className={
              !props.loadingRequest ? "cursor-pointer" : "cursor-default"
            }
            onClick={() =>
              !props.loadingRequest &&
              props.setIsTeachingDocument(!props.isTeachingDocument)
            }
          >
            {props.isTeachingDocument ? (
              <ImCheckboxChecked className="text-blue-600 w-8" />
            ) : (
              <ImCheckboxUnchecked className="text-gray-600 w-8" />
            )}
          </div>
        </div>
      )}
      {props.actionTaken === "approval" && (
        <div className="w-full px-2 mb-1 flex gap-2 items-center">
          <span className="text-gray-500 italic">
            Is Document for disability:
          </span>
          <div
            className={
              !props.loadingRequest ? "cursor-pointer" : "cursor-default"
            }
            onClick={() =>
              !props.loadingRequest &&
              props.setHasDisabilityDocument(!props.hasDisabilityDocument)
            }
          >
            {props.hasDisabilityDocument ? (
              <ImCheckboxChecked className="text-blue-600 w-8" />
            ) : (
              <ImCheckboxUnchecked className="text-gray-600 w-8" />
            )}
          </div>
        </div>
      )}
      <div className="w-full px-2">
        <textarea
          placeholder={`Comment for the ${props.actionTaken}`}
          cols={50}
          rows={3}
          className={`border border-${
            props.error.element === "comment" ? "red" : "gray"
          }-400 rounded px-3 py-1 w-full focus:outline-none`}
          style={{ resize: "none" }}
          value={props.responseMsg}
          onChange={(e) => {
            props.setResponseMsg(e.target.value);
            props.setError({ element: "", msg: "" });
          }}
          disabled={props.loadingRequest}
        ></textarea>
        {props.error.element === "comment" && (
          <p className="text-red-800">{props.error.msg}</p>
        )}
      </div>
      <div className="flex flex-row gap-1 max-w-full px-2">
        <button
          className="bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
          onClick={() => {
            props.setResponseMsg("");
            props.setActionTaken("");
            props.setError({ element: "", msg: "" });
          }}
          disabled={props.loadingRequest}
        >
          <RiArrowGoBackFill className="w-5 h-5" />
          <span>Back</span>
        </button>
        <button
          className="bg-blue-700 px-4 py-2 font-semibold rounded text-white focus:outline-none"
          disabled={props.loadingRequest}
          onClick={() =>
            window.confirm(
              `Are you sure you want to ${props.actionTaken} this document?`
            ) && props.handleVerify()
          }
        >
          {props.loadingRequest ? (
            <div className="">Loading...</div>
          ) : (
            <div className="inline-flex items-center space-x-2 gap-1">
              <FaRegCheckCircle className="w-5 h-5" />
              <span>Confirm</span>
            </div>
          )}
        </button>
      </div>
      {/* end right part */}
    </div>
  );
};
interface FooterInitialPropInterface {
  docStatus: "waiting" | "approved" | "rejected";
  loading: boolean;
  setActionTaken: (state: "" | "approval" | "rejection") => void;
  setResponse: (state: "approved" | "rejected") => void;
}

const FooterInitial: React.FC<FooterInitialPropInterface> = (
  props
): JSX.Element => {
  return (
    <div className="flex flex-row md:flex-row justify-end w-full items-center gap-2 overflow-x-auto">
      {/* right part */}
      {props.docStatus === "waiting" && (
        <div className="flex items-center gap-2 px-2">
          <button
            className={`px-4 py-2 font-semibold text-red-50 rounded inline-flex items-center space-x-2 focus:outline-none ${
              props.loading ? "bg-red-300 cursor-wait" : "bg-red-800"
            }`}
            onClick={() => {
              props.setActionTaken("rejection");
              props.setResponse("rejected");
            }}
            disabled={props.loading}
          >
            <MdCancel className="w-5 h-5" />
            <span>Reject</span>
          </button>
          <button
            className={`px-4 py-2 font-semibold text-blue-50 rounded inline-flex items-center space-x-2 focus:outline-none ${
              props.loading ? "bg-blue-400 cursor-wait" : "bg-blue-700"
            }`}
            onClick={() => {
              props.setActionTaken("approval");
              props.setResponse("approved");
            }}
            disabled={props.loading}
          >
            <FaRegCheckCircle className="w-5 h-5" />
            <span>Approve</span>
          </button>
        </div>
      )}
      {/* end right part */}
    </div>
  );
};

interface AppProps {
  doc: CandidateDetailsInterface;
  candidate: CandidatesInterface;
  responseMsg: string;
  loadingRequest: boolean;
  selectedSetting: DocCheckingContentType | null;
  setDoc?: (state: CandidateDetailsInterface | null) => void;
  setResponseMsg: (state: string) => void;
  setResponse: (state: "approved" | "rejected") => void;
  handleVerify: () => Promise<void>;
  setError: (data: { element: string; msg: string }) => void;
  setViewDoc: (state: boolean) => void;
  setCandidateId: (state: string) => void;
  setDocumentId: (state: string) => void;
  setViewCandidate: (state: boolean) => void;
  actionTaken: "" | "approval" | "rejection";
  setActionTaken: (state: "" | "approval" | "rejection") => void;
  document_index: number;
  setDocument_index: (state: number | null) => void;
  error: {
    element: string;
    msg: string;
  };
  documentCategories: DocumentReducerInterface;
  candidateList: CandidatesInterface[];
  isTeachingDocument: boolean;
  setIsTeachingDocument: (state: boolean) => void;
  hasDisabilityDocument: boolean;
  setHasDisabilityDocument: (state: boolean) => void;
}
const ApplFileView: React.FC<AppProps> = (props) => {
  const [isFullscreen, setSetFullscreen] = useState<boolean>(false);
  const handleFullScreen = () => {
    if (isFullscreen) {
      // alert("closing");
      fullScreen("doc-img", "CLOSE");
      setSetFullscreen(false);
    } else {
      // alert("opening");
      setSetFullscreen(true);
      fullScreen("doc-img", "OPEN");
    }
  };

  const handleNextPrev = (
    action: "NEXT" | "PREV",
    currentStatus: "approved" | "rejected" | "waiting" = "waiting"
  ) => {
    let detailsCopy = props.candidate.details.filter(
      (details) =>
        details.validation_status === "waiting" &&
        details.files &&
        details.files.length
    );
    let documentInArray =
      props.selectedSetting &&
      props.selectedSetting === DocCheckingContentType.documents
        ? DocumentInArray(props.documentCategories, props.candidateList)
        : detailsCopy;
    let newIndex: number = props.document_index;
    if (
      action === "NEXT" &&
      props.document_index <= documentInArray.length - 1
    ) {
      newIndex =
        currentStatus === "waiting"
          ? props.document_index + 1
          : props.document_index + 1;
      if (
        props.selectedSetting &&
        props.selectedSetting === DocCheckingContentType.documents
      ) {
        getDocumentFileByIndex(
          documentInArray as TableCandidatesInterface[],
          newIndex,
          props.setViewDoc,
          props.setDocumentId,
          props.setCandidateId,
          props.setDocument_index
        );
      } else {
        getCandidateFileByIndex(
          props.candidate,
          newIndex,
          props.setViewDoc,
          props.setDocumentId,
          props.setCandidateId,
          props.setDocument_index
        );
      }
    }
    if (action === "PREV" && props.document_index > 0) {
      newIndex = props.document_index - 1;
      if (
        props.selectedSetting &&
        props.selectedSetting === DocCheckingContentType.documents
      ) {
        getDocumentFileByIndex(
          documentInArray as TableCandidatesInterface[],
          newIndex,
          props.setViewDoc,
          props.setDocumentId,
          props.setCandidateId,
          props.setDocument_index
        );
      } else {
        getCandidateFileByIndex(
          props.candidate,
          newIndex,
          props.setViewDoc,
          props.setDocumentId,
          props.setCandidateId,
          props.setDocument_index
        );
      }
    }
    props.setIsTeachingDocument(false);
  };

  const isNextAllowed = () => {
    if (props.selectedSetting) {
      if (props.selectedSetting === DocCheckingContentType.documents) {
        let dataList = DocumentInArray(
          props.documentCategories,
          props.candidateList
        );
        let validDataList = dataList.filter(
          (item) => item.validation_status === "waiting"
        );
        if (validDataList.length <= 1) {
          // alert("ddd");
          // setKeepCurrentIndex(false);
          return false;
        }

        if (props.document_index <= dataList.length - 1) {
          if (
            dataList[props.document_index + 1] &&
            dataList[props.document_index + 1].validation_status === "waiting"
          ) {
            // alert("ddd");
            // setKeepCurrentIndex(false);
            return true;
          } else if (
            dataList[props.document_index] &&
            dataList[props.document_index].validation_status === "waiting"
          ) {
            // alert("yyy");
            // setKeepCurrentIndex(true);
            return true;
          }
        } else {
          return false;
        }
      } else {
        let detailsCopy = props.candidate.details.filter(
          (details) =>
            details.validation_status === "waiting" &&
            details.files &&
            details.files.length
        );
        if (props.document_index < detailsCopy.length - 1) {
          return true;
        } else {
          return false;
        }
      }
    } else {
      return false;
    }
  };

  const isPrevAllowed = () => {
    if (props.document_index > 0) {
      return true;
    }
    if (props.selectedSetting) {
      if (props.selectedSetting === DocCheckingContentType.documents) {
        let dataList = DocumentInArray(
          props.documentCategories,
          props.candidateList
        );
        if (
          dataList[props.document_index - 1] !== undefined &&
          !isEmptyOrSpaces(dataList[props.document_index - 1].files)
        ) {
          return true;
        } else {
          return false;
        }
      } else if (props.selectedSetting === DocCheckingContentType.names) {
        let detailsCopy = props.candidate.details.filter(
          (details) =>
            details.validation_status === "waiting" &&
            !isEmptyOrSpaces(details.files)
        );
        if (
          detailsCopy[props.document_index - 1] !== undefined &&
          !isEmptyOrSpaces(detailsCopy[props.document_index - 1].files)
        ) {
          return true;
        } else {
          return false;
        }
      }
    }
    return false;
  };

  return (
    <div className="bg-gray-50 h-screen grid grid-cols-1 md:grid-cols-3">
      {/* User Access Select */}
      {/* image content */}
      <div
        className={`col-span-2 h-auto md:max-h-screen overflow-y-auto relative bg-gray-200 group`}
      >
        {props.doc.files.length > 0 ? (
          isFileImage(getFileType(props.doc.files)) ? (
            <div className="relative">
              <img
                src={API.AUTH_FILES + props.doc.files}
                alt=""
                className={`w-full md:w-auto h-auto md:max-h-screen mx-auto`}
                id="doc-img"
                onDoubleClick={() => handleFullScreen()}
              />
            </div>
          ) : (
            <ViewPDF
              file_url={API.AUTH_FILES + props.doc.files}
              class_name={`w-full h-screen md:max-h-screen mx-auto`}
              frame_title={"applicatio PDF file view"}
            />
          )
        ) : (
          <div className="text-center text-red-400 py-8 animate__animated animate__fadeIn">
            <CgImage className="mx-auto w-40 h-40" />
            <span className="font-semibold text-4xl">No Files Provided</span>
          </div>
        )}
        <div className="flex items-center justify-between absolute top-0 w-full px-2 py-2">
          <div
            className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer"
            onClick={() => {
              props.selectedSetting !== DocCheckingContentType.names &&
                props.setCandidateId("");
              props.selectedSetting === DocCheckingContentType.names &&
                props.setViewCandidate(true);
              props.setViewDoc(false);
              props.setDocumentId("");
              props.setIsTeachingDocument(false);
            }}
          >
            <IoMdClose className="h-6 w-6 text-gray-500 group-hover:text-gray-800" />
          </div>
          {props.doc.files.length > 0 &&
            isFileImage(getFileType(props.doc.files)) && (
              <div className="flex items-center gap-2 opacity-0 group-hover:opacity-100">
                <div
                  className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                  onClick={() => zoomInOut("doc-img", "IN")}
                >
                  <BiZoomIn className="h-6 w-6 text-gray-500" />
                </div>
                <div
                  className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                  onClick={() => zoomInOut("doc-img", "OUT")}
                >
                  <BiZoomOut className="h-6 w-6 text-gray-500" />
                </div>
                <div
                  className="flex items-center bg-gray-100 justify-center h-10 w-10 rounded-full shadow-md opacity-4 cursor-pointer hover:bg-blue-50"
                  onClick={() => fullScreen("doc-img", "OPEN")}
                >
                  <AiOutlineFullscreen className="h-6 w-6 text-gray-500" />
                </div>
              </div>
            )}
        </div>

        {/* NEXT && PREV CONTROLLER ================================================= */}
        <div
          className={`absolute align-middle top-1/2 w-full flex ${
            !isPrevAllowed() ? "justify-end" : "justify-between"
          } z-20 opacity-0 group-hover:opacity-100 px-2`}
        >
          {isPrevAllowed() && (
            <button
              className={`inline-block bg-gray-100 cursor-pointer shadow-md rounded-full p-2 hover:bg-blue-50`}
              onClick={() => handleNextPrev("PREV")}
              disabled={props.loadingRequest}
            >
              <GrPrevious className="h-8 w-8 text-gray-500" />
            </button>
          )}
          {isNextAllowed() && (
            <button
              className={`inline-block bg-gray-100 cursor-pointer shadow-md rounded-full p-2 hover:bg-blue-50`}
              onClick={() =>
                handleNextPrev("NEXT", props.doc.validation_status)
              }
              disabled={props.loadingRequest}
            >
              <GrNext className="h-8 w-8 text-gray-500" />
            </button>
          )}
        </div>
        {/* END NEXT && PREV CONTROLLER ================================================= */}
      </div>
      {/* end image content */}

      {/* footer */}
      <section className="relative">
        <div className="w-full flex md:flex-col flex-col justify-between items-start gap-2">
          {/* CANDIDATE INFO PART */}
          <div className="flex items-center gap-4 w-full">
            {/* doc title & status */}
            <section className="p-3 w-full">
              <div className="flex items-center gap-2 border-b-2 my-2 py-1">
                <ImFolderOpen className="text-blue-600 text-xl" />
                <h2 className="uppercase tracking-wide text-xl font-semibold text-gray-700">
                  Document validation
                </h2>
              </div>
              <div className="flex flex-row items-center justify-left gap-2 mb-2">
                <span className="text-gray-500 italic">Doc Title: </span>
                <span className="font-bold text-2xl">
                  {GetDocTitle(props.documentCategories, props.doc).main}
                </span>
              </div>
              <div className="flex flex-row items-center justify-left gap-2 mb-2">
                <span className="text-gray-500 italic">Names: </span>
                <span className="font-medium text-lg">
                  {props.candidate.fname + " " + props.candidate.lname}
                </span>
              </div>
              <section className="animate__animated animate__fadeIn">
                <div className="flex flex-row items-center justify-left gap-2 mb-2">
                  <span className="text-gray-500 italic">National ID: </span>
                  <span className="font-medium text-lg">
                    {props.candidate.n_id}
                  </span>
                </div>
                <div className="flex flex-row items-center justify-left gap-2 mb-2">
                  <span className="text-gray-500 italic">Phone: </span>
                  <span className="font-medium text-lg">
                    {props.candidate.phone_number}
                  </span>
                </div>
                <div className="flex flex-row items-center justify-left gap-2 mb-2">
                  <span className="text-gray-500 italic">Email: </span>
                  <span className="font-medium text-lg">
                    {props.candidate.email}
                  </span>
                </div>
                <div className="flex flex-row items-center justify-left gap-2 mb-2">
                  <span className="text-gray-500 italic">District: </span>
                  <span className="font-medium text-lg">
                    {props.candidate.district}
                  </span>
                </div>
              </section>
              {/* status */}
              <div className="flex items-center gap-1">
                <span className="text-gray-500 italic">Status:</span>
                <span
                  className={`text-${
                    props.doc.validation_status === "waiting"
                      ? "yellow"
                      : props.doc.validation_status === "approved"
                      ? "blue"
                      : "gray"
                  }-700 font-semibold text-lg`}
                >
                  {props.doc.validation_status === "waiting"
                    ? "Pending"
                    : props.doc.validation_status === "approved"
                    ? "Approved"
                    : "Rejected"}
                </span>
              </div>
              {/* end status */}
            </section>
            {/* end doc title & status */}
          </div>
          {/* END CANDIDATE INFO PART */}
          {props.doc.files &&
            (!props.actionTaken ? (
              <FooterInitial
                docStatus={props.doc.validation_status}
                setActionTaken={props.setActionTaken}
                loading={props.loadingRequest}
                setResponse={props.setResponse}
              />
            ) : (
              <FooterResponse
                setActionTaken={props.setActionTaken}
                actionTaken={props.actionTaken}
                setResponseMsg={props.setResponseMsg}
                responseMsg={props.responseMsg}
                loadingRequest={props.loadingRequest}
                handleVerify={props.handleVerify}
                error={props.error}
                setError={props.setError}
                isTeachingDocument={props.isTeachingDocument}
                setIsTeachingDocument={props.setIsTeachingDocument}
                hasDisabilityDocument={props.hasDisabilityDocument}
                setHasDisabilityDocument={props.setHasDisabilityDocument}
              />
            ))}
        </div>
      </section>
      {/* end footer */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default connect(mapStateToProps, {})(ApplFileView);
